	module.exports = {"data":{"challengeNode":{"title":"Problem 217: Balanced Numbers","description":["A positive integer with k (decimal) digits is called balanced if its first ⌈k/2⌉ digits sum to the same value as its last ⌈k/2⌉ digits, where ⌈x⌉, pronounced ceiling of x, is the smallest integer ≥ x, thus ⌈π⌉ = 4 and ⌈5⌉ = 5.","So, for example, all palindromes are balanced, as is 13722.","Let T(n) be the sum of all balanced numbers less than 10n. ","Thus: T(1) = 45, T(2) = 540 and T(5) = 334795890. ","Find T(47) mod 315"],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-217-balanced-numbers","blockName":"Project Euler","tests":[{"text":"<code>euler217()</code> should return 6273134.","testString":"assert.strictEqual(euler217(), 6273134, '<code>euler217()</code> should return 6273134.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler217() {\n  // Good luck!\n  return true;\n}\n\neuler217();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-218-perfect-right-angled-triangles","id":"5900f4461000cf542c50ff58"},"slug":"/coding-interview-prep/project-euler/problem-217-balanced-numbers"}}

